    type: "Java (Android)",
    description:
      "Ta aplikacja została stworzona z myślą o ułatwieniu pracy konduktorów oraz motorniczych (logujących się na swoje konta) podczas pracy na Zabytkowej Linii Tramwajowej i nie tylko. Jej głównymi funkcjami jest: wskazywanie aktualnego opóźnienia podczas jazdy oraz ewentualnie odtwarzanie zapowiedzi przytankowych, oraz księgowanie sprzedaży biletów w bazie danych, więc każdy z konduktorów jest w stanie zobaczyć każdą sprzedaż w czasie rzeczywistym.",
